const header = document.querySelector("header");
const heroSection = document.querySelector("#hero");
const logoName = document.getElementById("logo-name");
const phoneLogo = document.getElementById("phone-logo");
const hambi = document.querySelector(".hamb")
const heroSectionOptions = {
    rootMargin: "-200px 0px 0px 0px"
}

const heroSectionObserver = new IntersectionObserver(function(
    entries,
    heroSectionObserver
    ) {
        entries.forEach(entry => {
            if(!entry.isIntersecting ) {
                header.style.background= "hsl(0, 0%, 85%)";
                header.style.color= "hsl(0, 0%, 23%)";
                logoName.style.display= "block";
                phoneLogo.style.transform= "translateY(0)";
                hambi.style.setProperty("--1", "#3b3b3b")

            } else {
                
                header.style.background= "rgba(0, 0, 0, 0.5)";
                header.style.color= "hsl(0, 0%, 85%)";
                logoName.style.display= "none";
                phoneLogo.style.transform= "translateY(-100%)";
                hambi.style.setProperty("--1", "#d9d9d9")

            }
        })
}, heroSectionOptions );

heroSectionObserver.observe(heroSection);

//for hamburger menu

const hamburger = document.querySelector(".nav-toggle");
const navLinks = document.querySelectorAll(".nav__link");
const navBox = document.getElementsByClassName("nav__list");

hamburger.addEventListener ('click', () => {
    document.body.classList.toggle('nav-open');
});

navLinks.forEach(link => {
    link.addEventListener('click', () => {
        document.body.classList.remove('nav-open');
    })
})